<template>
  <div class="setting-wrapper">
      
      
      


      <h1 class="bigtitle">My setting</h1>
      <yd-cell-group class="demo-small-pitch">
            <yd-cell-item type="link" href="/profile" arrow>
                <span slot="left">Edit Profile for roomie matching</span>

                <yd-switch slot="right" true-value=></yd-switch>

            </yd-cell-item>
            <p slot="bottom" style="text-align:left;padding:10px;color:#555;">If you wanna a roomie, you need finish this profile</p>
        </yd-cell-group>

         

         <yd-cell-group class="demo-small-pitch">
            <yd-cell-item type="label">
                <span slot="left">Turn off invitations</span>

                <yd-switch slot="right" v-model="invitation"> </yd-switch>

            </yd-cell-item>
            
        </yd-cell-group>

         <yd-cell-group class="demo-small-pitch">
            <yd-cell-item type="label" >
                <span slot="left">Turn off my profile</span>

                <yd-switch slot="right" v-model="profileon"></yd-switch>

            </yd-cell-item>
        </yd-cell-group>

       

        <yd-cell-group class="demo-small-pitch">
            <yd-cell-item type="link" href="/account" arrow>
                <span slot="left">Account infomation</span>
            </yd-cell-item>
            
        </yd-cell-group>

        
    <yd-button-group>
        <yd-button size="large" type="hollow" @click.native="Loginout">Login out</yd-button>
    </yd-button-group>

       
  </div>
</template>

<script>
import cookie from '../../static/js/cookie'
import {Cell, Switch as Vswitch , Accordion} from 'vue-ydui'
export default {
  data () {
    return {
        gender:[],
        invitation: true,
        profileon: true
    }
  },
  components: {
      Cell,
      Vswitch,
      Accordion
  },
  methods: {
      Loginout () {
          this.$router.push({path:'/login'})
      }
  }
}
</script>

<style lang="stylus" scoped>

</style>




